<template>
  <div class="courses">
    <!-- <h2></h2> -->
    <div class="handle">
      <!--  -->
      <div class="menu">
        <el-timeline>
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-input placeholder="" class="searchName" v-model="searchName">
                <!--                 <el-button slot="append" type="primary" icon="el-icon-search"></el-button>-->
              </el-input>
              <el-button type="primary" icon="el-icon-search" round></el-button>
            </el-card>
          </el-timeline-item>
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-form
                :model="ruleForm"
                :rules="rules"
                ref="ruleForm"
                label-width="80px"
                class="coursesForm"
              >
                <el-form-item label="" prop="Title">
                  <el-input v-model="ruleForm.Title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" required>
                  <el-col :span="11">
                    <el-form-item prop="date1">
                      <el-date-picker
                        type="date"
                        placeholder=""
                        v-model="ruleForm.date1"
                        style="width: 100%;"
                      >></el-date-picker>
                    </el-form-item>
                  </el-col>
                  <el-col class="line" :span="2">-</el-col>
                  <el-col :span="11">
                    <el-form-item prop="date2">
                      <el-time-picker
                        placeholder=""
                        v-model="ruleForm.date2"
                        style="width: 100%;"
                        format="HH:mm"
                        value-format="HH:mm"
                      ></el-time-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item label="" required>
                  <el-col :span="11">
                    <el-form-item prop="Date1">
                      <el-date-picker
                        type="date"
                        placeholder=""
                        v-model="ruleForm.Date1"
                        style="width: 100%;"
                      >></el-date-picker>
                    </el-form-item>
                  </el-col>
                  <el-col class="line" :span="2">-</el-col>
                  <el-col :span="11">
                    <el-form-item prop="Date2">
                      <el-time-picker
                        placeholder=""
                        v-model="ruleForm.Date2"
                        style="width: 100%;"
                        format="HH:mm"
                        value-format="HH:mm"
                      ></el-time-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item label="" prop="Title">
                  <el-input v-model="ruleForm.Place" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label="" prop="Introduction">
                  <el-input
                    type="textarea"
                    v-model="ruleForm.Introduction"
                    placeholder=""
                    maxlength="50"
                    :autosize="{ minRows: 4, maxRows: 6}"
                    show-word-limit
                  ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                  <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-timeline-item>
          <el-timeline-item placement="top">
            <el-card>
              <!--              <h4>created by &nbsp;<span style="font-weight:100;font-size:0.7em"></span></h4>-->
              <el-link :underline="false" style="padding-left:8em" @click="handleCheckCourses">
                
                <i class="el-icon-view el-icon--right"></i>
              </el-link>
            </el-card>
          </el-timeline-item>

        </el-timeline>
      </div>
    </div>

    <!--    -->
    <div class="formData">
      <el-table
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        :data="coursesData.filter(data => !searchName || data.Title.toLowerCase().includes(searchName.toLowerCase()))"
        border
        style="width: 100%"
        min-height="650"
        max-height="650"
      >
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column prop="Title" label="" width="80"></el-table-column>
        <el-table-column prop="Date" label="" sortable width="150"></el-table-column>
        <el-table-column prop="Place" label="" width="150"></el-table-column>
        <el-table-column prop="Enrollments.length" sortable label="" width="120"></el-table-column>
        <el-table-column prop="Introduction" label="" :formatter="checkNull"></el-table-column>
        <el-table-column fixed="right" label="" width="160">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="warning" size="small"></el-button>
            <el-tooltip
              content=""
              placement="top"
              effect="light"
              :hide-after="1000"
              :enterable="false"
            >
              <el-button
                @click="handleDelete(scope.$index, scope.row)"
                type="danger"
                size="small"
                icon="el-icon-delete"
                circle
                plain
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--      -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="updateForm" class="content" :rules="rules" ref="updateForm">
          <el-form-item label="" prop="Title" :label-width="formLabelWidth">
            <el-input v-model="updateForm.Title" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="" required>
            <el-col :span="5">
              <el-form-item prop="date1">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="updateForm.date1"
                  style="width: 100%;"
                >></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="5">
              <el-form-item prop="date2">
                <el-time-picker
                  placeholder=""
                  v-model="updateForm.date2"
                  style="width: 100%;"
                  format="HH:mm"
                  value-format="HH:mm"
                ></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="" required>
            <el-col :span="5">
              <el-form-item prop="Date1">
                <el-date-picker
                  type="date"
                  placeholder=""
                  v-model="updateForm.Date1"
                  style="width: 100%;"
                >></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="5">
              <el-form-item prop="Date2">
                <el-time-picker
                  placeholder=""
                  v-model="updateForm.Date2"
                  style="width: 100%;"
                  format="HH:mm"
                  value-format="HH:mm"
                ></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>

          <el-form-item label="" prop="Place" :label-width="formLabelWidth" required>
            <el-input v-model="updateForm.Place" autocomplete="off"></el-input>
          </el-form-item>

          <el-form-item label="" prop="Introduction" :label-width="formLabelWidth">
            <el-input
              type="textarea"
              v-model="updateForm.Introduction"
              placeholder=""
              maxlength="50"
              :autosize="{ minRows: 4, maxRows: 6}"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="updateStudent('updateForm')"> </el-button>
          <el-button @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogTableVisible">
      <el-table :data="teaCourseData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="Title" label="" width="150"></el-table-column>
        <el-table-column property="Date" label="" width="200"></el-table-column>
        <el-table-column property="Place" label="" width="200"></el-table-column>

      </el-table>
    </el-dialog>

  </div>
</template>
<script>
  import qs from "qs";
  export default {
    name: "course",
    data() {
      return {
        name: "liudaxia",
        searchName: "",
        coursesData: [],
        loading: true,
        //  
        ruleForm: {
          Title: "",
          Number: "",
          date1:"",
          date2:"",
          Date1:"",
          Date2:"",
          Place:"",
          Introduction: ""
        },
        //  
        updateForm: {
          Id: "",
          Title: "",
          date1:"",
          date2:"",
          Date1:"",
          Date2:"",
          Place:"",
          Introduction: ""
        },
        // 
        rules: {
          Title: [
            { required: true, message: "", trigger: "change" },
            { min: 2, max: 10, message: " 2 10 ", trigger: "blur" }
          ],
          Introduction: [
            {
              required: true,
              message: ",~",
              trigger: "change"
            }
          ]
        },
        dialogFormVisible: false,
        formLabelWidth: "6em",


        //
        teaCourseData: [],
        dialogTableVisible: false
      };
    },
    methods: {
      // 
      submitForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            // 
            var strData = {
              Title: this.ruleForm.Title,
              Date1: this.sendAddTime(
                this.ruleForm.date1,
                this.ruleForm.date2
              ),
              Date2: this.sendAddTime(
                this.ruleForm.Date1,
                this.ruleForm.Date2
              ),
              Place: this.ruleForm.Place,
              Introduction: this.ruleForm.Introduction
            };
            // post
            this.axios
              .post("http://localhost:8004/Courses/create", qs.stringify(strData))
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o",
                  type: "danger"
                });
              });
          } else {
            this.$message.error("");
            return false;
          }
        });
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      //
      indexMethod(index) {
        return index + 1;
      },

      // 
      handleDelete(index, row) {
        // console.log(index, row);
        this.$confirm(", ?", "", {
          cancelButtonText: "",
          confirmButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.axios
              .get("http://localhost:8004/Courses/Delete?id=" + row.Id)
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o",
                  type: "danger"
                });
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      },

      handleClick(row) {
        // 
        console.log(row);
        // this.resetForm(updateForm);
        this.updateForm.Title = row.Title;
        this.updateForm.Introduction = row.Introduction;
        this.updateForm.Id = row.Id;
        this.updateForm.Place = row.Place;
        this.updateForm.date1 = this.getDateOne(row.Date);
        this.updateForm.date2 = this.getDateTwo(row.Date);
        console.log(this.updateForm.date1)
        this.dialogFormVisible = true;
      },

      //
      handleCheckCourses(){
        this.dialogTableVisible = true;
        this.teaCourseData = [
          {
            Title: '',
            Date: '2020-06-13 15:00',
            Place: '',
          },
          {
            Title: '',
            Date: '2020-06-12 19:00',
            Place: '',
          }

        ]

      },
      // 
      updateStudent(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            var updateData = {
              Id: this.updateForm.Id,
              Title: this.updateForm.Title,
              EnrollmentDate1: this.sendAddTime(
                this.updateForm.date1,
                this.updateForm.date2
              ),
              EnrollmentDate2: this.sendAddTime(
                this.updateForm.Date1,
                this.updateForm.Date2
              ),
              Place: this.updateForm.Place,
              Introduction: this.updateForm.Introduction
            };
            this.dialogFormVisible = false;
            //  post  
            this.axios
              .post(
                "http://localhost:8004/Courses/Edit",
                qs.stringify(updateData)
              )
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$notify({
                    id: "",
                    title: "",
                    message: "",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o",
                  type: "danger"
                });
              });
          } else {
            this.$message.error("");
            return false;
          }
        });
      },

      // 
      checkNull(row, column) {
        // console.log(row)
        return row.Introduction == null
          ? " "
          : row.Introduction;
      },
      // 
      getAllcourses() {
        // this.axios.get("http://localhost:8004/Courses").then(result => {
        //   if (result.status == 200) {
        //     // console.log(result);
        //     this.coursesData = result.data;
        //     this.coursesData.forEach(item => {
        //       console.log(item.Id);
        //       if (item.Introduction == null) {
        //         item.Introduction == "";
        //       }
        //       // item.Introduction==null ? "0" : item.Introduction
        //     });
        //     this.loading = false;
        //   }
        // });
        this.coursesData=[
          {
            Id:1,
            Title: '',
            Date: '2020-06-13 15:00',
            Place: '',
            Introduction: ''

          },
          {
            Id:2,
            Title: '',
            Date: '2020-06-12 19:00',
            Place: '',
            Introduction: ''

          }];
        this.coursesData.forEach(item => {
          console.log(item.Id);
          if (item.Introduction == null) {
            item.Introduction == "";
          }
          // item.Introduction==null ? "0" : item.Introduction
        });
        console.log(this.coursesData)
        this.loading = false;
      },

      getDateOne(time) {
        var timeArr = time.split(" ");
        var d = timeArr[0].split("-");
        return new Date(d[0], d[1] - 1, d[2]);
      },
      getDateTwo(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split(":");
        return t[0] + ":" + t[1];
      },

      // data date2
      sendAddTime(date, date2) {
        var Y = date.getFullYear();
        var M =
          date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1;
        var MT = parseInt(M) + 1;
        var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
        var addTime = Y + "-" + M + "-" + D + " " + date2+":00";
        console.log(addTime);
        return addTime;
      }
    },
    mounted() {
      this.getAllcourses();
    }
  };
</script>
<style lang="less" scoped>
  .courses {
    display: flex;
    height: 42em;
    .handle {
      flex: 2;
      // height: 50em;
      // background-color: lightblue;
      .searchName {
        width: 70%;
      }
      .menu {
        padding: 5% 10% 0 10%;
        h4 {
          margin: 0.2em;
          padding-bottom: 0.5em;
        }
        .stydentForm {
          .el-form-item__content {
            // margin: 0 !important;
            .el-input__suffix {
              right: 0 !important;
            }
            .el-input {
              width: 70%;
            }
          }
        }
      }
    }
    .formData {
      flex: 3;
      text-align: center;
      .el-table {
        text-align: center !important;
        float: right;
        // padding: 5%
        margin: 2%;
      }
      // background-color: lightpink;
    }
  }
</style>



