<template>
  <div class="student">
    <!-- <h2></h2> -->
    <div class="handle">
      <!--  -->
      <div class="menu">
        <el-timeline>
          <!---->
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-input placeholder="" class="searchName" v-model="searchName">
              </el-input>
              <el-button type="primary" icon="el-icon-search" round></el-button>
            </el-card>
          </el-timeline-item>
          <!-- -->
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-form :model="stuInfo" ref="ruleForm" label-width="80px" class="studentForm">
                <el-form-item label="" prop="stuUsername" :label-width="formLabelWidth">
                  <el-input v-model="stuInfo.stuUsername" readonly></el-input>
                </el-form-item>
                <el-form-item label="" prop="stuTell" :label-width="formLabelWidth">
                  <el-input v-model="stuInfo.stuTell" readonly></el-input>
                </el-form-item>
                <el-form-item label="" prop="stuTotalHour" :label-width="formLabelWidth">
                  <el-input v-model="stuInfo.stuTotalHour" readonly></el-input>
                </el-form-item>
                <el-form-item label="" prop="stuRestHour" :label-width="formLabelWidth">
                  <el-input v-model="stuInfo.stuRestHour" readonly></el-input>
                </el-form-item>
                <el-form-item :label-width="formLabelWidth">
                  <el-button type="primary" @click="beforModifyStuInfo"></el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-timeline-item>
          <!-- -->
          <el-timeline-item placement="top">
            <el-card>
              <el-link :underline="false" style="padding-left:8em" @click="handleCheckCourses">
                
                <i class="el-icon-view el-icon--right"></i>
              </el-link>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </div>

    <!-- -->
    <div class="formData">
      <el-table
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        :data="coursesData.filter(data => !searchName || data.Title.toLowerCase().includes(searchName.toLowerCase())|| data.Place.toLowerCase().includes(searchName.toLowerCase()))"
        border
        style="width: 100%"
        min-height="650"
        max-height="650"
      >
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column prop="courseLocation" label="" width="120"></el-table-column>
        <el-table-column prop="courseDate" label="" sortable width="200"></el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button @click="checkCourseInfo(scope.row)" type="primary" size="small">
                <strong>{{scope.row.interest}}</strong>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="" width="100" align="center" class-name="status-col">
          <template slot-scope="{row}">
            <!--            <el-tag :type="row.isAttend | statusFilter">-->
            <el-tag :type="row.state==='' ? 'danger':row.state===''? 'success':'gray'">
              {{ row.state }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" effect="light" :hide-after="2000">
              <el-button @click="handleCourses(scope.row)" type="primary" size="small">
                
                <strong>{{scope.row.Enrollments.length}}</strong>
                
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="180" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button v-if="row.state===''" size="mini" type="primary" @click="modifystate(row,'')">
                
              </el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button v-if="row.state===''" size="mini" type="warning" @click="modifystate(row,'')">
                
              </el-button>
            </el-tooltip>
            <el-tooltip content="o()o" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button v-if="(row.state==='')" size="mini" type="danger" @click="modifystate(row,'')">
                
              </el-button>
            </el-tooltip>
            <el-tooltip content="(*)" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button v-if="row.state===''" size="mini" type="success" @click="modifystate(row,'')">
                
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--      -->
      <el-dialog title="" :visible.sync="courseInfoVisible">
        <el-form :model="courseInfo" class="content" ref="updateForm">
          <el-form-item label="" prop="courseId" :label-width="formLabelWidth" required>
            <el-input v-model="courseInfo.courseId" autocomplete="off" readonly></el-input>
          </el-form-item>
          <el-form-item label="" prop="interest" :label-width="formLabelWidth" required>
            <el-input v-model="courseInfo.interest" autocomplete="off" readonly></el-input>
          </el-form-item>
          <el-form-item label="" prop="courseCostHour" :label-width="formLabelWidth" required>
            <el-input v-model="courseInfo.courseCostHour" autocomplete="off" readonly></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth" required>
            <el-col :span="5">
              <el-form-item prop="date">
                <el-date-picker type="date" placeholder="" v-model="courseInfo.date" style="width: 100%;"
                                readonly></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="5">
              <el-form-item prop="time1">
                <el-time-picker placeholder="" v-model="courseInfo.time1" style="width: 100%;" format="HH:mm"
                                value-format="HH:mm" readonly></el-time-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="5">
              <el-form-item prop="time2">
                <el-time-picker placeholder="" v-model="courseInfo.time2" style="width: 100%;" format="HH:mm"
                                value-format="HH:mm" readonly></el-time-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="courseLocation" :label-width="formLabelWidth" required>
            <el-input v-model="courseInfo.courseLocation" autocomplete="off" readonly></el-input>
          </el-form-item>
          <el-form-item label="" prop="courseDescription" :label-width="formLabelWidth">
            <el-input type="textarea" v-model="courseInfo.courseDescription" placeholder="" maxlength="50"
                      :autosize="{ minRows: 4, maxRows: 6}" show-word-limit readonly></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="courseInfoVisible = false"> </el-button>
        </div>
      </el-dialog>

    </div>

    <!-- -->
    <el-dialog title="" :visible.sync="stuChooseFormVisible" class="dialogOne">
      <el-table :data="stuChooseData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="stuUsername" label=""></el-table-column>
      </el-table>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :rules="rules" :visible.sync="updateStuInfoDialogVisible" class="dialogOne">
      <el-form :model="updateStuInfo" class="content" :rules="rules" ref="updateStuInfo">
        <el-form-item label="" prop="stuUsername" :label-width="formLabelWidth">
          <el-input v-model="updateStuInfo.stuUsername" autocomplete="off" required></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuTell" :label-width="formLabelWidth">
          <el-input v-model="updateStuInfo.stuTell" autocomplete="off" required></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuPassword" :label-width="formLabelWidth">
          <el-input v-model="updateStuInfo.stuPassword" placeholder="..." autocomplete="off" required></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword" :label-width="formLabelWidth">
          <el-input v-model="updateStuInfo.confirmPassword" placeholder="..." autocomplete="off"
                    required></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="modifyStuInfo"></el-button>
        <el-button @click="updateStuInfoDialogVisible = false"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogStuClassFormVisible" class="dialogOne">
      <el-table :data="stuCourseData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="courseId" label="" width="100"></el-table-column>
        <el-table-column property="interest" label="" width="100"></el-table-column>
        <el-table-column property="courseDate" label="" sortable width="200"></el-table-column>
        <el-table-column property="courseLocation" label="" width="150"></el-table-column>
      </el-table>
    </el-dialog>

  </div>
</template>
<script>
  import qs from "qs";

  export default {
    name: "students",
    data() {
      return {
        name: "lisa",
        searchName: "",
        loading: true,

        // 
        rules: {
          stuUsername: [
            {required: true, message: "", trigger: "change"},
            {min: 2, max: 10, message: "210", trigger: "blur"}
          ],
          stuPassword: [
            {required: true, message: "", trigger: "change"},
            {min: 6, max: 15, message: "615", trigger: "blur"}
          ],
          confirmPassword: [
            {required: true, message: "", trigger: "change"},
            {min: 6, max: 15, message: "615", trigger: "blur"}
          ],
          stuTell: [
            {required: true, message: "", trigger: "change"},
            {min: 11, max: 11, message: "", trigger: "blur"}
          ],
        },


        //  
        stuInfo: {
          stuUsername: '',
          stuTell: '',
          stuTotalHour: '',
          stuRestHour: ''
        },

        // 
        updateStuInfoDialogVisible: false,
        updateStuInfo: {
          stuUsername: '',
          stuTell: '',
          stuPassword: '',
          confirmPassword: '',
        },

        // 
        coursesData: [],
        courseInfoVisible: false,
        formLabelWidth: "7em",

        //  
        courseInfo: {
          courseId: "",
          courseCostHour: "",
          interest: "",
          date: "",
          time1: "",
          time2: "",
          courseLocation: "",
          courseDescription: ""
        },

        //
        stuChooseData: [],
        stuChooseFormVisible: false,

        //
        stuCourseData: [],
        dialogStuClassFormVisible: false,

      };
    },
    methods: {
      //
      indexMethod(index) {
        return index + 1;
      },
      //
      getDateOne(time) {
        var timeArr = time.split(" ");
        var d = timeArr[0].split("-");
        return new Date(d[0], d[1] - 1, d[2]);
      },
      getDateTwo(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[0].split(":");
        return h[0] + ":" + h[1];
      },
      getDateThree(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[1].split(":");
        return h[0] + ":" + h[1];

      },

      // 
      beforModifyStuInfo() {
        this.updateStuInfoDialogVisible = true;
        //
        this.updateStuInfo.stuUsername = this.stuInfo.stuUsername;
        this.updateStuInfo.stuTell = this.stuInfo.stuTell;
        this.updateStuInfo.stuPassword = this.updateStuInfo.confirmPassword;
      },
      modifyStuInfo() {
        //
        var updateStuData = {
          stuUsername: this.updateStuInfo.stuUsername,
          stuTell: this.updateStuInfo.stuTell,
          stuPassword: this.updateStuInfo.stuPassword
        };

        //
        if (this.updateStuInfo.stuPassword != this.updateStuInfo.confirmPassword) {
          // 
          this.$message({
            message: "",
            type: "failed"
          });
        } else {
          //  post
          // todo updateStuData
          // 
          this.axios
            .post("http://localhost:8081/stu/updateInformation", qs.stringify(updateStuData))
            .then(result => {
              if (result.data == 'success') {
                this.$notify({
                  id: "",
                  title: "",
                  message: "",
                  type: "success"
                });
                this.global.role = this.updateStuInfo.stuUsername;
                console.log(this.global.role);
                this.getStuInfo();
              }
            })
            .catch(err => {
              this.$message({
                message: "o()o" + err + '',
                type: "danger"
              });
            });

        }

      },


      //
      checkCourseInfo(row) {
        this.courseInfoVisible = true;
        // 
        this.courseInfo.courseId = row.courseId;
        this.courseInfo.interest = row.interest;
        this.courseInfo.courseCostHour = row.courseCostHour;
        this.courseInfo.courseDescription = row.courseDescription;
        this.courseInfo.courseLocation = row.courseLocation;
        this.courseInfo.date = this.getDateOne(row.courseDate);
        this.courseInfo.time1 = this.getDateTwo(row.courseDate);
        this.courseInfo.time2 = this.getDateThree(row.courseDate);
      },

      //
      handleCourses(row) {
        this.stuChooseFormVisible = true;
        this.stuChooseData = []
        row.Enrollments.forEach(item => {
          var newStu = {
            stuUsername: item.stuUsername,
          };
          this.stuChooseData.push(newStu);
        });
      },

      //  
      modifystate(row, state) { //state
        if (row.state == '' && state == '') {
          this.$message({
            message: '',
            type: 'success'
          })
        }
        else if (state == '') {
          //todo attendClassId -1
          // attendClassIdisCanceledByStu0isAttend0
          this.axios
            .post("http://localhost:8081/stu/addCourse", qs.stringify({'courseId': row.courseId}))
            .then(result => {
              if (result.data=='success') {
              row.state = state;
                this.$notify({
                  message: "",
                  type: "success"
                });
              }
            })
            .catch(err => {
              this.$message({
                message: "o()o" + err + '',
                type: "danger"
              });
            });
        }
        else if (state == '') {
          //attendClassIdisCanceledByStu0isAttend1
          this.axios
            .post("http://localhost:8081/stu/sign", qs.stringify({'courseId': row.courseId}))
            .then(result => {
              if (result.data=='success') {
              row.state = state;
                this.$notify({
                  message: "()",
                  type: "success"
                });
              }
            })
            .catch(err => {
              this.$message({
                message: "o()o" + err + '',
                type: "danger"
              });
            });
          this.$message({
            message: '',
            type: 'success'
          })
        }
        // 
        else if (state == '') {
          // attendClassIdisCanceledByStu1
          this.axios
            .post("http://localhost:8081/stu/cancelCourse", qs.stringify({'courseId': row.courseId}))
            .then(result => {
              if (result.data=='success') {
              row.state = state;
                this.$notify({
                  message: "()",
                  type: "success"
                });
              }
            })
            .catch(err => {
              this.$message({
                message: "o()o" + err + '',
                type: "danger"
              });
            });
          this.$message({
            message: '',
            type: 'success'
          })
        }
        row.state = state;

      },

      //
      handleCheckCourses() {
        this.dialogStuClassFormVisible = true;
        //todo 
        //
         this.axios.get("http://localhost:8081/stu/queryCourseAttend").then(result => {
           if (result) {
             this.stuCourseData = result.data
           }
         });
        // this.stuCourseData = [
        //   {
        //     courseId: '1',
        //     interest: '',
        //     courseDate: '2020-06-13 15:00-17:00',
        //     courseLocation: '',
        //     // teaUsername: ''
        //   },
        //   {
        //     courseId: '12',
        //     interest: '',
        //     courseDate: '2020-06-12 19:00-21:00',
        //     courseLocation: '',
        //     // teaUsername: ''
        //   }
        // ]
      },

      //
      getStuInfo() {
        //todo 
        //
         this.axios.get("http://localhost:8081/stu/getMyInfo").then(result => {
           if (result) {
             this.stuInfo=result.data;
           }
         });
      },

      //
      getAllcourses() {
        //todo 
        // 
        this.axios.get("http://localhost:8081/stu/queryAllCourse").then(result => {
          if (result) {
            this.coursesData = [];
            result.data.forEach(item => {
              var course = {
                attendClassId: item.attendClassId,
                courseId: item.courseId,
                interest: item.interest,
                courseCostHour: item.courseCostHour,
                courseDate: item.courseDate,
                courseLocation: item.courseLocation,
                //state: item.isCanceledByStu === '1' ? '' : item.isAttend === '1' ? '' : '',
                state: item.isAttend === '1' ? '' : item.isCanceledByStu === '1' ? '' : '',
                isAttend: item.isAttend,
                isCanceledByStu: item.isCanceledByStu,
                Enrollments: item.Enrollments,
                courseDescription: item.courseDescription,

              };
              this.coursesData.push(course);
            });
            this.loading = false;
          }
        });
      },

    },
    mounted() {
      console.log(this.$root.levelid);
      this.getStuInfo();
      this.getAllcourses();
    }
  };
</script>
<style lang="less" scoped>
  .student {
    display: flex;
    height: 42em;

    .handle {
      flex: 2;
      // height: 50em;
      // background-color: lightblue;
      .searchName {
        width: 70%;
      }

      .menu {
        padding: 5% 10% 0 10%;

        h4 {
          margin: 0.2em;
          padding-bottom: 0.5em;
        }

        .studentForm {
          .el-form-item__content {
            // margin: 0 !important;
            .el-input__suffix {
              right: 0 !important;
            }

            .el-input {
              width: 70%;
            }
          }
        }
      }
    }

    .formData {
      flex: 3;
      text-align: center;

      .el-table {
        text-align: center !important;
        float: right;
        margin: 2%;
      }

      // background-color: lightpink;
    }

    .dialogOne {
      .el-form-item__content {
        margin: 0 !important;

        .el-input__suffix {
          right: 0 !important;
        }

        .el-input {
          width: 70%;
        }
      }
    }
  }
</style>



