<template>
  <div class="admin">
    <div class="handle">
      <!-- -->
      <div class="menu">
        <el-timeline>
          <!--  -->
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-form :model="checkForm" ref="checkForm" label-width="6em" class="checkForm">
                <el-row>
                  <el-form-item label="">
                    <el-input placeholder="" class="searchName" v-model="searchName" @input="change($event)">
                    </el-input>
                    <el-button type="primary" @click="searchRecord()" icon="el-icon-search" round></el-button>
                  </el-form-item>
                </el-row>
                <el-row>
                  <el-col :span="13">
                    <el-form-item label="" prop="totalHour">
                      <el-input v-model="checkForm.totalHour" placeholder="100" readonly></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11">
                    <el-form-item label="" prop="restHour" readonly>
                      <el-input v-model="checkForm.restHour" placeholder="0" readonly></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item>
                  <el-button type="primary" @click="addHour()"></el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-timeline-item>

          <!-- -->
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-form :model="teaInfo" :rules="rules" ref="teaInfo" label-width="80px" class="adminForm">
                <el-form-item label="" prop="teaUsername" required>
                  <el-input v-model="teaInfo.teaUsername" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="teaPassword" required>
                  <el-input v-model="teaInfo.teaPassword" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="teaTell" required>
                  <el-input v-model="teaInfo.teaTell" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="addTea('teaInfo')"></el-button>
                  <el-button @click="resetForm('teaInfo')"></el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-timeline-item>

          <!--  -->
          <el-timeline-item placement="top">
            <el-card>
              <el-link :underline="false" style="padding-left:8em" @click="handleCheckTea()">
                
                <i class="el-icon-view el-icon--right"></i>
              </el-link>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </div>

    <!-- -->
    <div class="formData">
      <el-table v-loading="loading" element-loading-text="" element-loading-spinner="el-icon-loading"
                :data="studentData.filter(data => !searchName || data.stuUsername.toLowerCase().includes(searchName.toLowerCase()))"
                border style="width: 100%" min-height="650" max-height="650">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column prop="stuUsername" label="" width="100"></el-table-column>
        <el-table-column prop="courseDate" sortable label="" width="200"></el-table-column>
        <el-table-column prop="courseLocation" label="" width="150"></el-table-column>
        <el-table-column prop="courseId" label="" width="100"></el-table-column>
        <el-table-column prop="interest" label="" width="100"></el-table-column>
        <el-table-column fixed="right" label="" width="160">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button @click="handleCourseInfo(scope.row)" type="warning" size="small"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button @click="handleDelete(scope.$index, scope.row)" type="danger" size="small" icon="el-icon-delete"
                         circle plain></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- -->
      <el-dialog title="" :visible.sync="dialogFormVisible" class="dialogOne">
        <el-form :model="addClassHourForm" ref="addClassHourForm" class="content">
          <el-form-item label="" prop="stuUsername" :label-width="formLabelWidth">
            <el-input v-model="addClassHourForm.stuUsername" autocomplete="off" readonly></el-input>
          </el-form-item>
          <el-form-item label="" prop="restClassHour" :label-width="formLabelWidth" readonly>
            <el-input v-model="addClassHourForm.restClassHour" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="addClassHour" :label-width="formLabelWidth">
            <el-input v-model="addClassHourForm.addClassHour" placeholder="" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="updateStudent"> </el-button>
          <el-button @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>
    </div>

    <!-- -->
    <el-dialog title="" :visible.sync="dialogCourseVisible" class="dialogOne">
      <el-form :model="courseForm" class="content" :rules="rules" ref="addClassHourForm">
        <el-form-item label="" prop="courseId" :label-width="formLabelWidth" required>
          <el-input v-model="courseForm.courseId" autocomplete="off" readonly></el-input>
        </el-form-item>
        <el-form-item label="" prop="interest" :label-width="formLabelWidth" required>
          <el-input v-model="courseForm.interest" autocomplete="off" readonly></el-input>
        </el-form-item>
        <el-form-item label="" prop="courseCostHour" :label-width="formLabelWidth" required>
          <el-input v-model="courseForm.courseCostHour" autocomplete="off" readonly></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date">
              <el-date-picker type="date" placeholder="" v-model="courseForm.date" style="width: 100%;"
                              readonly></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="6">
            <el-form-item prop="time1">
              <el-time-picker placeholder="" v-model="courseForm.time1" style="width: 95%;" format="HH:mm"
                              value-format="HH:mm" readonly></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="1">-</el-col>
          <el-col :span="6">
            <el-form-item prop="time2">
              <el-time-picker placeholder="" v-model="courseForm.time2" style="width: 95%;" format="HH:mm"
                              value-format="HH:mm" readonly></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="courseLocation" :label-width="formLabelWidth" required>
          <el-input v-model="courseForm.courseLocation" autocomplete="off" readonly></el-input>
        </el-form-item>

        <el-form-item label="" prop="courseDescription" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="courseForm.courseDescription" placeholder="" maxlength="50"
                    :autosize="{ minRows: 4, maxRows: 6}" show-word-limit readonly></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogCourseVisible = false"> </el-button>
      </div>
    </el-dialog>


    <!--  -->
    <el-dialog title="" :visible.sync="dialogTableVisible">
      <el-table :data="teaData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="teaId" label="" width="150"></el-table-column>
        <el-table-column property="teaUsername" label="" width="150"></el-table-column>
        <el-table-column property="teaHour" label="" width="150"></el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <el-tooltip content="" placement="right" effect="light" :hide-after="2000">
              <el-button
                @click="deleteTea(scope.$index,scope.row)"
                type="danger"
                size="small"
                icon="el-icon-delete"
                circle
                plain
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
  import qs from "qs";

  export default {
    name: "admin",
    data() {
      return {
        name: "lisa",
        searchName: "",
        studentData: [], //
        loading: true,
        delay: parseInt(200),

        // 
        checkForm: {
          totalHour: "",
          restHour: ""
        },

        //  
        teaInfo: {
          teaUsername: "",
          teaPassword: "",
          teaTell: ""
        },

        //  
        addClassHourForm: {
          teaUsername: "",
          restClassHour: "",
          addClassHour: ""
        },

        courseForm: {
          courseId: "",
          courseCostHour: "",
          interest: "",
          date: "",
          time1: "",
          time2: "",
          courseLocation: "",
          courseDescription: ""
        },
        // 
        rules: {
          teaUsername: [
            {required: true, message: "", trigger: "change"},
            {min: 2, max: 10, message: "210", trigger: "blur"}
          ],
          teaPassword: [
            {required: true, message: "", trigger: "change"},
            {min: 6, max: 15, message: "615", trigger: "blur"}
          ],
          teaTell: [
            {required: true, message: "", trigger: "change"},
            {min: 11, max: 11, message: "", trigger: "blur"}
          ],
        },

        //
        dialogFormVisible: false,
        formLabelWidth: "6em",

        //
        courseInfo: [],
        dialogCourseVisible: false,
        formLabelWidth: "6em",

        //
        teaData: [],
        dialogTableVisible: false
      };
    },
    methods: {
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      //
      indexMethod(index) {
        return index + 1;
      },
      //
      getDateOne(time) {
        var timeArr = time.split(" ");
        var d = timeArr[0].split("-");
        return new Date(d[0], d[1] - 1, d[2]);
      },
      getDateTwo(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[0].split(":");
        return h[0] + ":" + h[1];
      },
      getDateThree(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[1].split(":");
        return h[0] + ":" + h[1];

      },

      // 
      searchRecord() {

        //todo searchName
        // this.axios.get("http://localhost:8004/Courses/Edit?id=" + item.CourseId).then(result => {
        //   if (result.status == 200) {
        //     this.checkForm.totalHour = result.data.stuTotalClassHour;
        //     this.checkForm.restHour = result.data.stuTotalClassHour;
        //   }
        // });
        var result = {
          stuTotalClassHour: '7',
          stuRemainingClassHour: '2',
        }
        this.checkForm.totalHour = result.stuTotalClassHour;
        this.checkForm.restHour = result.stuRemainingClassHour;
      },

      //
      addHour() {
        //
        this.dialogFormVisible = true;
        this.addClassHourForm.stuUsername = this.searchName;
        this.addClassHourForm.restClassHour = this.checkForm.restHour;
      },
      updateStudent() {
        //
        //this.dialogFormVisible = false;
        var updateData = {
          stuUsername: this.addClassHourForm.stuUsername,
          addHourNumber: this.addClassHourForm.addClassHour
        };
        // post
        //todo updateData
        this.axios
          .post("http://localhost:8004/Students/Edit", qs.stringify(updateData))
          .then(result => {
            if (result.status == 200 || result.status == 302) {
              this.$notify({
                id: "",
                title: "",
                message: "",
                type: "success"
              });
              this.searchRecord();
              this.addClassHourForm.restClassHour = this.checkForm.restHour;
              this.resetForm('addClassHourForm');

            }
          })
          .catch(err => {
            this.$message({
              message: "o()o" + err + '',
              type: "danger"
            });
          });
      },

      // 
      addTea(formName) {
        // console.log(formName);
        this.$refs[formName].validate(valid => {
          if (valid) {
            // 
            var strData = {
              teaUsername: this.teaInfo.teaUsername,
              teaPassword: this.teaInfo.teaPassword,
              teaTell: this.teaInfo.teaTell,
            };
            console.log(strData);
            // post
            this.axios
              .post(
                "http://localhost:8004/Students/create",
                qs.stringify(strData)
              )
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getStudentData();
                  this.resetForm("teaInfo");
                }
              })
              .catch(err => {
                console.log(err)
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          } else {
            this.$message.error("");
            // console.log("error submit!!");
            return false;
          }
        });
      },

      // 
      handleCourseInfo(row) {
        this.dialogCourseVisible = true;

        this.courseForm.courseId = row.courseId;
        this.courseForm.interest = row.interest;
        this.courseForm.date = this.getDateOne(row.courseDate);
        this.courseForm.time1 = this.getDateTwo(row.courseDate);
        this.courseForm.time2 = this.getDateThree(row.courseDate);
        this.courseForm.courseLocation = row.courseLocation;
        this.courseForm.courseCostHour = row.courseCostHour;
        this.courseForm.courseDescription = row.courseDescription;
        //T courseId 
        // this.$http.get("http://localhost:8004/Students").then(result => {
        //   if (result.status == 200) {
        //     // console.log(result.data);
        //     this.studentData = result.data;
        //
        //   }
        // });
        // var result = {
        //   courseId: '1',
        //   courseCostHour: '1',
        //   courseDescription: '',
        // };
        // this.courseForm.courseCostHour = result.courseCostHour;
        // this.courseForm.courseDescription = result.courseDescription;
      },

      // 
      handleCheckTea() {
        this.dialogTableVisible = true;
        this.getTeaData();
      },
      getTeaData() {
        //todo  
        // this.axios.get("http://localhost:8004/Courses/Edit?id=").then(result=>{
        //   console.log(result.data);
        //   this.teaData=result.data;
        // });
        this.teaData = [
          {
            teaId: '14758',
            teaUsername: '',
            teaTotalClassHour: '4',

          },
          {
            teaId: '21209',
            teaUsername: '',
            teaTotalClassHour: '4'

          }];
      },

      // 
      deleteTea(index, row) {
        //todo   
        this.$confirm(", ?", "", {
          cancelButtonText: "",
          confirmButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.axios
              .get("http://localhost:8004/Enrollments/Delete?id=" + row.teaId)
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getTeaData();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      },

      // 
      handleDelete(index, row) {
        console.log(row.CourseId);
        this.$confirm(", ?", "", {
          cancelButtonText: "",
          confirmButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.axios
              .get("http://localhost:8004/Students/Delete?StuId=" + row.StuId + '&courseId=' + row.CourseId)
              .then(result => {
                if (result.status == 200 || result.status == 302) {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getStudentData();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      },

      // 
      getStudentData() {
        //todo
        // this.$http.get("http://localhost:8004/Students").then(result => {
        //   if (result.status == 200) {
        //     // console.log(result.data);
        //     this.studentData = result.data;
        //     this.loading = false;
        //   }
        // });
        this.studentData =
          [{
            courseId: '1',
            interest: '',
            courseCostHour: '1',
            courseDate: '2020-06-13 15:00-17:00',
            courseLocation: '',
            stuUsername: '',
            // Enrollments: [{
            //   stuId: '11',
            //   stuUsername: 'www',
            //   stuTell: '110'
            // }],
            courseDescription: ''
          },
            {
              courseId: '12',
              interest: '',
              courseCostHour: '2',
              courseDate: '2020-06-12 19:00-21:00',
              courseLocation: '',
              stuUsername: '',
              // Enrollments: [{
              //   stuId: '12',
              //   stuUsername: 'wy',
              //   stuTell: '120'
              // },
              //   {
              //     stuId: '3',
              //     stuUsername: 'yy',
              //     stuTell: '119'
              //   }
              // ],
              courseDescription: ''
            }
          ];
        this.loading = false;
      },

    },
    //
    // loading
    mounted() {
      this.getStudentData();
    }
  };
</script>
<style lang="less" scoped>
  .admin {
    display: flex;
    height: 42em;

    .handle {
      flex: 2;

      .menu {
        padding: 5% 10% 0 10%;

        h4 {
          margin: 0.2em;
          padding-bottom: 0.5em;
        }

        .adminForm {
          .searchName {
            width: 70%;
          }

          .el-form-item__content {
            /*margin: 0 !important;*/

            .el-input__suffix {
              right: 0 !important;
            }

            .el-input {
              width: 70%;
            }
          }
        }

        .checkForm {
          .searchName {
            width: 40%;
          }

          .el-form-item__content {
            // margin: 0 !important;
            .el-input__suffix {
              right: 0 !important;
            }

            .el-input {
              width: 60%;
            }
          }
        }
      }
    }

    .formData {
      flex: 3;
      text-align: center;

      .el-table {
        text-align: center !important;
        float: right;
        // padding: 5%
        margin: 2%;
      }

      // background-color: lightpink;
    }

    .dialogOne {
      .el-form-item__content {
        margin: 0 !important;

        .el-input__suffix {
          right: 0 !important;
        }

        .el-input {
          width: 70%;
        }
      }
    }
  }
</style>



