<template>
  <div class="coach">
    <!-- <h2></h2> -->
    <div class="handle">
      <!--  -->
      <div class="menu">
        <el-timeline>
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-input placeholder="" class="searchName" v-model="searchName">
              </el-input>
              <el-button type="primary" icon="el-icon-search" round></el-button>
            </el-card>
          </el-timeline-item>
          <el-timeline-item placement="top">
            <el-card>
              <h4></h4>
              <el-form :model="addCourseForm" :rules="rules" ref="addCourseForm" label-width="80px" class="coursesForm">
                <el-form-item label="" prop="interest" required>
                  <el-select v-model="addCourseForm.interest" filterable placeholder="">
                    <el-option v-for="item in interest" :key="item.value" :label="item.interest"
                               :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="  " prop="courseCostHour" required>
                  <el-input v-model="addCourseForm.courseCostHour" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" required>
                  <el-col :span="11">
                    <el-form-item prop="date">
                      <el-date-picker type="date" placeholder="" v-model="addCourseForm.date" style="width: 100%;">
                      </el-date-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item label="" required>
                  <el-col :span="11">
                    <el-form-item prop="time1">
                      <el-time-picker placeholder="" v-model="addCourseForm.time1" style="width: 100%;"
                                      format="HH:mm" value-format="HH:mm"></el-time-picker>
                    </el-form-item>
                  </el-col>
                  <el-col class="line" :span="1">-</el-col>
                  <el-col :span="11">
                    <el-form-item prop="time2">
                      <el-time-picker placeholder="" v-model="addCourseForm.time2" style="width: 100%;"
                                      format="HH:mm" value-format="HH:mm"></el-time-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item label="" prop="courseLocation">
                  <el-input v-model="addCourseForm.courseLocation" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="courseDescription">
                  <el-input
                    type="textarea"
                    v-model="addCourseForm.courseDescription"
                    placeholder=""
                    maxlength="50"
                    :autosize="{ minRows: 4, maxRows: 6}"
                    show-word-limit
                  ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitForm('addCourseForm')"></el-button>
                  <el-button @click="resetForm('addCourseForm')"></el-button>
                </el-form-item>
              </el-form>
            </el-card>
          </el-timeline-item>
          <el-timeline-item placement="top">
            <el-card>
              <el-link :underline="false" style="padding-left:8em" @click="handleCheckCourses">
                
                <i class="el-icon-view el-icon--right"></i>
              </el-link>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </div>

    <!-- -->
    <div class="formData">
      <el-table
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        :data="coursesData.filter(data => !searchName || data.interest.toLowerCase().includes(searchName.toLowerCase())|| data.courseLocation.toLowerCase().includes(searchName.toLowerCase()))"
        border
        style="width: 100%"
        min-height="650"
        max-height="650"
      >
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="courseId" label="" width="80"></el-table-column>
        <el-table-column prop="interest" label="" width="100"></el-table-column>
        <el-table-column prop="courseDate" label="" sortable width="200"></el-table-column>
        <el-table-column prop="courseLocation" label="" width="150"></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" effect="light" :hide-after="2000">
              <el-button @click="handleCourses(scope.row)" type="success" size="small">
                
                <strong>{{scope.row.Enrollments.length}}</strong>
                
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="160">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button @click="handleClick(scope.row)" type="warning" size="small"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top" effect="light" :hide-after="1000" :enterable="false">
              <el-button @click="handleDelete(scope.$index, scope.row)" type="danger" size="small" icon="el-icon-delete"
                         circle plain></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </div>


    <!-- -->
    <el-dialog title="" :visible.sync="dialogFormVisible" class="dialogOne">
      <el-form :model="updateForm" class="content" :rules="rules" ref="updateForm">
        <el-form-item label="" prop="courseId" :label-width="formLabelWidth" required>
          <el-input v-model="updateForm.courseId" autocomplete="off" readonly></el-input>
        </el-form-item>
        <el-form-item label="" prop="interest" :label-width="formLabelWidth" required>
          <el-select v-model="updateForm.interest" filterable placeholder="">
            <el-option v-for="item in interest" :key="item.value" :label="item.interest"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="courseCostHour" :label-width="formLabelWidth" required>
          <el-input v-model="updateForm.courseCostHour" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date">
              <el-date-picker type="date" placeholder="" v-model="updateForm.date"
                              style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="6">
            <el-form-item prop="time1">
              <el-time-picker placeholder="" v-model="updateForm.time1" style="width: 95%;" format="HH:mm"
                              value-format="HH:mm"></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="1">-</el-col>
          <el-col :span="6">
            <el-form-item prop="time2">
              <el-time-picker
                placeholder=""
                v-model="updateForm.time2"
                style="width: 95%;"
                format="HH:mm"
                value-format="HH:mm"
              ></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="courseLocation" :label-width="formLabelWidth" required>
          <el-input v-model="updateForm.courseLocation" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="courseDescription" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            v-model="updateForm.courseDescription"
            placeholder=""
            maxlength="50"
            :autosize="{ minRows: 4, maxRows: 6}"
            show-word-limit
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="updateStudent('updateForm')"></el-button>
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!--    </div>-->

    <!-- -->
    <el-dialog title="" :visible.sync="dialogTableVisible">
      <el-table :data="stuChooseData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="courseId" label="" width="100"></el-table-column>
        <el-table-column property="stuUsername" label="" width="100"></el-table-column>
        <el-table-column property="stuTell" label="" width="150"></el-table-column>
        <el-table-column property="isAttend" label="" width="100" class-name="status-col">
          <template slot-scope="{row}">
            <el-tag :type="[row.isAttend==='' ? 'danger':'success']">
              {{ row.isAttend }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip content="" placement="right" effect="light" :hide-after="2000">
              <el-button @click="deleteCourse(scope.$index,scope.row)" type="danger" size="small" icon="el-icon-delete"
                         circle plain></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogTeaClassFormVisible">
      <el-table :data="teaCourseData">
        <el-table-column type="index" :index="indexMethod"></el-table-column>
        <el-table-column property="courseId" label="" width="100"></el-table-column>
        <el-table-column property="interest" label="" width="100"></el-table-column>
        <el-table-column property="courseDate" label="" sortable width="200"></el-table-column>
        <el-table-column property="courseLocation" label="" width="150"></el-table-column>
      </el-table>
    </el-dialog>

  </div>
</template>
<script>
  import qs from "qs";

  export default {
    name: "coach",

    data() {
      return {
        name: "lisa",
        searchName: "",
        coursesData: [],
        loading: true,

        interest: [
          {interest: "", value: 0},
          {interest: "", value: 1},
          {interest: "", value: 2},
          {interest: "", value: 3},
        ],


        //  
        addCourseForm: {
          interest: "",
          courseCostHour: "",
          date: "",
          time1: "",
          time2: "",
          courseLocation: "",
          courseDescription: ""
        },
        //  
        updateForm: {
          courseId: "",
          courseCostHour: "",
          interest: "",
          date: "",
          time1: "",
          time2: "",
          courseLocation: "",
          courseDescription: ""
        },
        // 
        rules: {
          interest: [{required: true, message: "", trigger: "blur"}],
          courseCostHour: [{required: true, message: "", trigger: "blur"}],
          date: [{required: true, message: "", trigger: "blur"}],
          time1: [{required: true, message: "", trigger: "blur"}],
          time2: [{required: true, message: "", trigger: "blur"}],
          courseLocation: [{required: true, message: "", trigger: "blur"}],
          courseDescription: [{required: true, message: ",~", trigger: "change"}]
        },
        dialogFormVisible: false,
        formLabelWidth: "6em",

        //
        stuChooseData: [],
        dialogTableVisible: false,

        //
        teaCourseData: [],
        dialogTeaClassFormVisible: false
      };
    },
    methods: {
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      // 
      indexMethod(index) {
        return index + 1;
      },
      //
      getDateOne(time) {
        var timeArr = time.split(" ");
        var d = timeArr[0].split("-");
        return new Date(d[0], d[1] - 1, d[2]);
      },
      getDateTwo(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[0].split(":");
        return h[0] + ":" + h[1];
      },
      getDateThree(time) {
        var timeArr = time.split(" ");
        var t = timeArr[1].split("-");
        var h = t[1].split(":");
        return h[0] + ":" + h[1];

      },
      // data date2
      sendAddTime(date, date2) {
        var Y = date.getFullYear();
        var M =
          date.getMonth() + 1 < 10
            ? "0" + (date.getMonth() + 1)
            : date.getMonth() + 1;
        var MT = parseInt(M) + 1;
        var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
        var addTime = Y + "-" + M + "-" + D + " " + date2 + ":00";
        console.log(addTime);
        return addTime;
      },

      // 
      submitForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            // 
            var course = {
              interest: this.interest[this.addCourseForm.interest].interest,
              courseCostHour: this.addCourseForm.courseCostHour,
              courseStartDate: this.sendAddTime(
                this.addCourseForm.date,
                this.addCourseForm.time1
              ),
              courseEndDate: this.sendAddTime(
                this.addCourseForm.date,
                this.addCourseForm.time2
              ),
              courseLocation: this.addCourseForm.courseLocation,
              courseDescription: this.addCourseForm.courseDescription
            };
            // post
            this.axios
              .post("http://localhost:8081/tea/addCourse", qs.stringify(course))
              .then(result => {
                if (result.data === 'success') {
                  this.resetForm('addCourseForm');
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.resetForm('addCourseForm');
                this.$message({
                  message: "o()o," + err + '',
                  type: "danger"
                });
              });
          } else {
            this.$message.error("");
            return false;
          }
        });
      },

      //  
      handleDelete(index, row) {
        this.$confirm(", ?", "", {
          cancelButtonText: "",
          confirmButtonText: "",
          type: "warning"
        })
          .then(() => {
            //TODO isCanceledByTea1
            this.axios
              .post("http://localhost:8004/Coach", qs.stringify({'courseId': row.courseId}))
              .then(result => {
                if (result.data === 'success') {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      },

      // 
      deleteCourse(index, row) {
        this.$confirm(", ?", "", {
          cancelButtonText: "",
          confirmButtonText: "",
          type: "warning"
        })
          .then(() => {
            //todo   attendClassIdisCancelByStu1
            // row.courseID row.stuUsername 
            this.axios
              .post("http://localhost:8004/Coach", qs.stringify({'attendClassId': row.courseId}))
              .then(result => {
                if (result.data === 'sucess') {
                  this.$message({
                    message: "(*)",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });

      },

      //
      handleCourses(row) {
        this.dialogTableVisible = true;
        this.stuChooseData = []
        row.Enrollments.forEach(item => {
          var newCourse = {
            // courseId: row.courseId,
            courseId : item.attendClassId,
            stuUsername: item.stuUsername,
            stuTell: item.stuTell,
            isAttend: item.isAttend === '0' ? '' : ''
          };
          this.stuChooseData.push(newCourse);
        });
      },

      //
      handleClick(row) {
        // 
        // this.resetForm(updateForm);
        this.updateForm.courseId = row.courseId;
        this.updateForm.interest = row.interest;
        this.updateForm.courseCostHour = row.courseCostHour;
        this.updateForm.courseDescription = row.courseDescription;
        this.updateForm.courseLocation = row.courseLocation;
        this.updateForm.date = this.getDateOne(row.courseDate);
        this.updateForm.time1 = this.getDateTwo(row.courseDate);
        this.updateForm.time2 = this.getDateThree(row.courseDate);

        this.dialogFormVisible = true;
      },

      //
      handleCheckCourses() {
        this.dialogTeaClassFormVisible = true;
        // todo
        // 
        // this.axios.post("http://localhost:8004/Coach").then(result => {
        //   if (result) {
        //     this.teaCourseData = [];
        //     result.data.forEach(item => {
        //       var teaCourse = {
        //         courseId: item.courseId,
        //         interest: item.interest,
        //         courseDate: item.courseDate,
        //         courseLocation: item.courseLocation,
        //       };
        //       this.teaCourseData.push(teaCourse);
        //     });
        //   }
        // });


        var result = [
          {
            courseId: '1',
            interest: '',
            courseDate: '2020-06-13 15:00-17:00',
            courseLocation: '',
          },
          {
            courseId: '12',
            interest: '',
            courseDate: '2020-06-12 19:00-21:00',
            courseLocation: '',
          }
        ];
        this.teaCourseData = [];
        result.forEach(item => {
          var teaCourse = {
            courseId: item.courseId,
            interest: item.interest,
            courseDate: item.courseDate,
            courseLocation: item.courseLocation,
          };
          this.teaCourseData.push(teaCourse);
        });
      },

      // 
      updateStudent(formName) {
        console.log(formName + 'hi');
        this.$refs[formName].validate(valid => {
          if (valid) {
            var updateData = {
              courseId: this.updateForm.courseId,
              interest: this.updateForm.interest,
              courseCostHour: this.updateForm.courseCostHour,
              courseStartDate: this.sendAddTime(this.updateForm.date, this.updateForm.time1),
              courseEndDate: this.sendAddTime(this.updateForm.date, this.updateForm.time2),
              courseLocation: this.updateForm.courseLocation,
              courseDescription: this.updateForm.courseDescription
            };
            this.dialogFormVisible = false;
            //TODO updateData 
            this.axios
              .post("http://localhost:8004/Coach", qs.stringify(updateData))
              .then(result => {
                if (result.data === 'sucess') {
                  this.$notify({
                    id: "",
                    title: "",
                    message: "",
                    type: "success"
                  });
                  this.getAllcourses();
                }
              })
              .catch(err => {
                this.$message({
                  message: "o()o" + err + '',
                  type: "danger"
                });
              });
          } else {
            this.$message.error("");
            return false;
          }
        });
      },

      // 
      getAllcourses() {

        this.axios.get("http://localhost:8081/tea/queryMyCourse").then(result => {
          if (result) {
            this.coursesData = result.data;
            this.loading = false;
          }
        });

      //   this.coursesData =
      //     [{
      //       courseId: '1',
      //       interest: '',
      //       courseCostHour: '1',
      //       courseDate: '2020-06-13 15:00-17:00',
      //       courseLocation: '',
      //       isCanceledByTea: '0',
      //       Enrollments: [{
      //         attendClassId:'1',
      //         stuId: '11',
      //         stuUsername: 'www',
      //         stuTell: '110',
      //         isAttend: '0',
      //       }],
      //       courseDescription: ''
      //     },
      //       {
      //         courseId: '12',
      //         interest: '',
      //         courseCostHour: '2',
      //         courseDate: '2020-06-12 19:00-21:00',
      //         courseLocation: '',
      //         isCanceledByTea: '0',
      //         Enrollments: [{
      //           attendClassId:'2',
      //           stuId: '12',
      //           stuUsername: 'wy',
      //           stuTell: '120',
      //           isAttend: '0',
      //         },
      //           {
      //             attendClassId:'3',
      //             stuId: '3',
      //             stuUsername: 'yy',
      //             stuTell: '119',
      //             isAttend: '1'
      //           }
      //         ],
      //         courseDescription: '',
      //         isAttend: '1',
      //       }
      //     ];
      //   this.loading = false;
       },

      goBack() {
        this.$root.role =3;
        this.$root.username = '';
        sessionStorage.clear();
        window.history.back();
      },

    },
    mounted() {
      this.getAllcourses();
      //
      if (window.history && window.history.pushState) {
        // 
        history.pushState(null, null, document.URL);
        window.addEventListener('popstate', this.goBack, false);
      }
    },
    destroyed() {
      window.removeEventListener('popstate', this.goBack, false);
    },
  };
</script>
<style lang="less" scoped>
  .coach {
    display: flex;
    height: 42em;

    .handle {
      flex: 2;
      // height: 50em;
      // background-color: lightblue;
      .searchName {
        width: 70%;
      }

      .menu {
        padding: 5% 10% 0 10%;

        h4 {
          margin: 0.2em;
          padding-bottom: 0.5em;
        }

        .studentForm {
          .el-form-item__content {
            // margin: 0 !important;
            .el-input__suffix {
              right: 0 !important;
            }

            .el-input {
              width: 70%;
            }
          }
        }
      }
    }

    .formData {
      flex: 3;
      text-align: center;

      .el-table {
        text-align: center !important;
        float: right;
        margin: 2%;
      }

      // background-color: lightpink;
    }

    .dialogOne {
      .el-form-item__content {
        margin: 0 !important;

        .el-input__suffix {
          right: 0 !important;
        }

        .el-input {
          width: 70%;
        }
      }
    }
  }
</style>



